export default {
	onShareAppMessage(res) {
		return {
			title: this.key,
			path: 'pages/goods/goods-list/goods-list?key='+this.key,
			success: (res) => {
				console.log(res);
			},
			fail: (res) => {
				console.log(res);
			}
		}
	},
	data() {
		return {
			key:'',
			goodsList:[],
			loadingText:"正在加载中...",
			headerTop:"0px",
			headerPosition:"fixed",
			orderbyList:[
				{text:"销量",selected:true,orderbyicon:false,orderby:0},
				{text:"价格",selected:false,orderbyicon:['sheng','jiang'],orderby:0},
				{text:"好评",selected:false,orderbyicon:false,orderby:0}
			],
			orderby:"sheng",
			pageCount:1,
			pageid:1,
		};
	},
	onLoad: function (option) { 
		this.key=option.key
		uni.setNavigationBarTitle({
			title: option.key
		});
		this.reload();
		//兼容H5下排序栏位置










	},
	onPageScroll(e){
		//兼容iOS端下拉时顶部漂移
		if(e.scrollTop>=0){
			this.headerPosition = "fixed";
		}else{
			this.headerPosition = "absolute";
		}
	},
	//下拉刷新，需要自己在page.json文件中配置开启页面下拉刷新 "enablePullDownRefresh": true
	onPullDownRefresh() {
		this.goodsList=[]
	    setTimeout(()=>{
			this.reload();
	        uni.stopPullDownRefresh();
	    }, 1000);
	},
	//上拉加载，需要自己在page.json文件中配置"onReachBottomDistance"
	onReachBottom(){			
		this.pageid++;
		if(this.pageid>this.pageCount){
			this.loadingText="到底了";
			return false;
		}else{
			this.reload();
			this.loadingText="正在加载...";
		}
	},
	methods:{
		reload(){				
			let data={
				key: this.key,					
				pageid: this.pageid
			}
			this.$xm.post('/Product/search',data,(res)=>{
				if(res.count==0){
					this.loadingText="没有找到您要搜索的商品！";
				}else{						
					res.prolist.map(ele=>{
						ele.pro_sn='http://img.xmvogue.com/thumb/' + ele.pro_sn + '.jpg?x-oss-process=style/300';
						ele.pro_name=ele.pro_name.slice(0,15)+'...'
					})
					this.goodsList=this.goodsList.concat(res.prolist)
					this.pageCount=res.count;
					if(this.pageCount==1){
						this.loadingText='到底了'
					}	
				}
								
			})
		},
		toSearch(){
			uni.setNavigationBarTitle({
				title: this.key
			});
			this.reload();
		},
		//商品跳转
		toGoods(e){				
			uni.navigateTo({
				url: '../../goods/goods?gid=' + e.id
			});
		},
		// 加入购物车
		joinCart(e){
			let data={
				proid: e.id,
				pronum: 1
			}
			this.$xm.post('/Cart/add',data,(res)=>{					
				let result=res.msg;
				uni.showToast({
					title: result
				});					
			})				
		},
		
	}
	
}
